:root {
  --scale: 8;
  --primary-color: #3b82f6;
  --primary-hover: #2563eb;
  --secondary-color: #f3f4f6;
  --text-color: #1f2937;
  --border-color: #d1d5db;
  --shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -1px #0000000f;
}

body {
  color: var(--text-color);
  background-color: #f9fafb;
  margin: 0;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
}

h3 {
  color: var(--text-color);
  margin-bottom: 1rem;
  font-weight: 600;
}

.guessLine {
  font-size: 1.1rem;
  display: inline;
}

.container {
  box-shadow: var(--shadow);
  background: #fff;
  border-radius: 12px;
  max-width: 600px;
  margin: 30px auto;
  padding: 20px;
}

#guessText {
  color: var(--primary-color);
  font-size: 24px;
  font-weight: bold;
}

#canvasDrawing {
  border: 2px solid var(--border-color);
  width: calc(28px * var(--scale));
  height: calc(28px * var(--scale));
  touch-action: none;
  box-shadow: var(--shadow);
  background: #fff;
  border-radius: 8px;
  margin: 0 auto;
  display: block;
}

button {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  margin: 5px;
  padding: 10px 20px;
  font-size: .95rem;
  font-weight: 500;
  transition: background-color .2s;
}

button:hover {
  background-color: var(--primary-hover);
}

button:active {
  transform: translateY(1px);
}

input[type="number"] {
  border: 1px solid var(--border-color);
  box-sizing: border-box;
  border-radius: 6px;
  width: 100%;
  margin: 8px 0;
  padding: 8px 12px;
  font-size: .95rem;
}

input[type="number"]:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px #3b82f633;
}

label {
  color: var(--text-color);
  margin-top: 15px;
  font-weight: 500;
  display: block;
}

#extrasDiv {
  display: none;
}

#chartDiv {
  background: var(--secondary-color);
  border-radius: 8px;
  margin-top: 15px;
  padding: 20px;
}

#canvasGraph {
  margin: 0 auto;
  display: block;
}

.button-group {
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 15px;
  display: flex;
}

input[type="file"] {
  margin-bottom: 10px;
}

@media (width <= 640px) {
  body {
    padding: 10px;
  }

  .container {
    margin: 15px auto;
    padding: 15px;
  }

  button {
    width: 100%;
    margin: 5px 0;
  }

  .button-group {
    flex-direction: column;
  }
}
